.mainBlock h2{margin:0;padding:75px 0 25px 0;font-size:16px;font-weight:400;line-height:19px;text-align:center;letter-spacing:.06em;color:rgba(34,53,111,.5)}.mainBlock li{list-style:none;box-sizing:border-box;color:#fff}.mainBlock li div{border-top:1px solid #fff;position:relative}.mainBlock li div span{font-size:32px;line-height:34px;font-weight:600;padding-left:25px}.mainBlock li div span::before{position:absolute;top:20px;left:0px;content:"●";font-size:20px;color:#e9b730}.mainBlock .topList{padding:0;display:grid;grid-template-columns:repeat(4, 1fr);margin:0;padding-bottom:120px}.mainBlock .topList li{height:360px;padding:70px 50px}.mainBlock .topList li div{padding-top:25px}.mainBlock .topList .li1{background:#ffe2de}.mainBlock .topList .li2{background:#c4e769}.mainBlock .topList .li3{background:#62d0df}.mainBlock .topList .li4{background:#0052c1}.mainBlock .middleList{padding:0;display:grid;grid-template-columns:repeat(3, 1fr);margin:0 0 22px 0}.mainBlock .middleList li{height:480px;padding:100px 70px}.mainBlock .middleList li div{padding-top:30px}.mainBlock .middleList li div span{display:block;max-width:150px;padding-left:30px}.mainBlock .middleList li div span::before{top:30px;left:0px;font-size:30px}.mainBlock .middleList .li1{background:#22356f}.mainBlock .middleList .li2{background:#0052c1}.mainBlock .middleList .li4{background:#62d0df}.mainBlock .middleList .li5{background:#dee1ff}.mainBlock .middleList .li6{background:#8f00ff}.mainBlock .bottomList{padding:0;margin-bottom:40px;display:grid;grid-template-columns:repeat(3, 1fr)}.mainBlock .bottomList li{height:240px;padding:55px 105px}.mainBlock .bottomList li h3{font-weight:400;font-size:28px;line-height:33px;color:#22356f}.mainBlock .bottomList li p{font-size:16px;line-height:25px;letter-spacing:-0.03em;color:rgba(34,53,111,.5)}