.contactUsBlock{margin:0;height:800px;background-color:#22356f;padding-left:180px;display:flex;justify-content:space-between}.contactUsBlock img{height:100%}.contactUsBlock .infoBlock{padding:103px 0px 0px 0px;color:#fff}.contactUsBlock .infoBlock h3{font-weight:300;margin:0;font-size:16px;line-height:12px;letter-spacing:.06em}.contactUsBlock .infoBlock h4{margin:230px 0 50px 0;font-size:64px;line-height:94px;letter-spacing:-0.01em}.contactUsBlock .infoBlock p{max-width:580px;margin:0;font-size:36px;line-height:53px;letter-spacing:-0.005em}.contactUsBlock .infoBlock button{color:#62d0df;padding:0;background-color:transparent;border:none;cursor:pointer;position:relative;margin-top:80px;font-size:16px;line-height:23px;font-weight:600;letter-spacing:.1em}.contactUsBlock .infoBlock button::after{content:"";position:absolute;width:120%;transform:scaleX(0);height:2px;bottom:-3px;left:-10%;background-color:#62d0df;transform-origin:bottom right;transition:transform .25s ease-out}.contactUsBlock .infoBlock button:hover::after{transform:scaleX(1);transform-origin:bottom left}.contacts{display:flex;font-size:14px;line-height:23px;letter-spacing:-0.006em;color:rgba(34,53,111,.5)}.contacts .contactsItems{padding:100px 70px;display:grid;column-gap:60px;grid-template-columns:repeat(3, 1fr)}.contacts h4{margin:0 0 60px 0;font-size:14px;line-height:11px;color:#22356f}.contacts .phoneNumber{margin:25px 0 0 0}.contacts .email{color:#006dff;margin:0}.contacts .timetable p{margin:0}footer{background:#62d0df;height:80px;padding-left:195px;display:flex;align-items:center}footer p{font-size:14px;font-weight:600;line-height:11px;color:#fff;position:relative}footer p::before{position:absolute;font-weight:400;top:2px;left:-15px;content:"©"}